import styled from "styled-components";
import {urlBuilder} from "../../../additional.js";
import {useCallback} from "react";

export default function TeacherCard(props) {
    const teacher = props.teacher;

    const [lastName = "", firstName = "", middleName = ""] = teacher.title.trim().split(/\s+/);
    const getTitleLesson = useCallback((lessonCode) => {
        if(lessonCode === 'MATHEMATICS') return `üìê –ú–∞—Ç–µ–º–∞—Ç–∏–∫–∞`;
        if(lessonCode === 'INFORMATICS') return `üíª –ò–Ω—Ñ–æ—Ä–º–∞—Ç–∏–∫–∞`;
        if(lessonCode === 'PHYSICS') return `üß≤ –§–∏–∑–∏–∫–∞`;
        return null;
    }, [])
    return (
        <TeacherCardStyled>
            <div className="teacher-avatar-box">
                <div className="teacher-avatar">
                    <img src={urlBuilder(teacher.image)} alt="photo"/>
                </div>
            </div>
            <div className="teacher-name-box">
                <div className="teacher-name">
                    <p>
                        {lastName && <span className="last-name">{lastName}</span>}
                        {firstName && <span className="first-name">{firstName}</span>}
                        {middleName && <span className="middle-name">{middleName}</span>}
                    </p>
                </div>

                <div className="teacher-lesson">
                    {teacher.types.length > 0 && teacher.types.map((type, indexType) => (
                        <div key={indexType}
                             className={`teacher-lesson-item ${type.title}`}>
                            <p>{getTitleLesson(type.title)}</p>
                        </div>
                    ))}
                </div>

            </div>
        </TeacherCardStyled>
    )
}

const TeacherCardStyled = styled.div`
    display: flex;
    flex-direction: column;
    position: relative;
    background-color: white;
    box-shadow: 0 4px 8px 0 #3232470F, 0 4px 4px 0 #32324714;
    border-radius: 8px;
    width: calc((100% - (6 * 24px)) / 7);
    transition: all 0.3s ease;

    @media screen and (max-width: 1450px){
        width: calc((100% - (3 * 24px)) / 4);
    }
    @media screen and (max-width: 1200px){
        width: calc((100% - (2 * 24px)) / 3);
    }
    @media screen and (max-width: 650px){
        width: calc((100% - 24px) / 2);
    }
    @media screen and (max-width: 450px){
        width: calc(100%);
        flex-direction: row;
        gap: 24px;
    }
    @media screen and (max-width: 350px){
        flex-direction: column;
        width: calc(100%);
        gap: 0;
        
    }
    

    & > div.teacher-avatar-box {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        position: relative;
        padding: 20px 0;
        @media screen and (max-width: 450px){
            padding: 0 20px;
        }
        @media screen and (max-width: 350px){
            padding: 20px 0;
        }

        & > div.teacher-avatar {
            display: flex;
            aspect-ratio: 1;
            width: 90%;
            border-radius: 16px;
            overflow: hidden;
            position: relative;
            @media screen and (max-width: 450px){
                height: 92px;
                width: 92px;
            }
            @media screen and (max-width: 350px){
                width: 128px;
                height: auto;
                aspect-ratio: 1;
            }

            & > img {
                filter: contrast(1);
                height: 100%;
                width: 100%;
                object-fit: cover;
                transition: all 0.3s ease-in-out;
            }
        }
    }
    
    &:hover > div.teacher-avatar-box > div.teacher-avatar > img {
        transform: scale(1.1);
        filter: contrast(1.1);
    }

    & > div.teacher-name-box {
        display: flex;
        flex-direction: column;
        padding: 20px 0;

        & > div.teacher-name {
            display: flex;
            margin: 0 1em;
            position: relative;
            box-sizing: border-box;
            justify-content: center;

            & > p {
                display: flex;
                flex-direction: column;
                text-align: center;
                margin: 0;
                padding: 0;
                @media screen and (max-width: 450px){
                    flex-direction: row;
                    flex-wrap: wrap;
                    align-items: center;
                    justify-content: center;
                    gap: 6px;
                }
                
                & > span {
                    font-weight: 500;
                    font-size: clamp(16px, 1vw, 22px);
                    line-height: 120%;
                    letter-spacing: 0;
                    color: #212529;
                    
                    &.first-name {
                        order: 2;
                        color: #1B9E49;
                        font-size: clamp(22px, 1vw, 28px);
                        @media screen and (max-width: 450px){
                            order: 1;
                            width: 100%;
                        }
                    }
                    &.last-name {
                        order: 1;
                        @media screen and (max-width: 450px){
                            order: 3;
                        }
                    }
                    &.middle-name {
                        order: 3;
                        @media screen and (max-width: 450px){
                            order: 2;
                        }
                    }
                }

            }
        }
        
        & > div.teacher-lesson {
            display: flex;
            justify-content: center;
            padding: 20px 0;
            position: relative;
            @media screen and (max-width: 450px){
                padding: 10px 0;
            }
            & > div.teacher-lesson-item {
                display: flex;
                background-color: #2f4858;
                height: 32px;
                justify-content: center;
                align-items: center;
                padding: 0 18px;
                border-radius: 18px;
                @media screen and (max-width: 450px){
                    height: 28px;
                    padding: 0 9px;
                }
                & > p {
                    margin: 0;
                    padding: 0;
                    @media screen and (max-width: 450px){
                        font-size: clamp(15px, 2vw, 22px);
                    }
                }
                
                &.MATHEMATICS {
                    background-color: #A3D5B8;
                    & > p {
                        color: #518366;
                    }
                }
                
                &.INFORMATICS {
                    background-color: #D5A3A3;
                    & > p {
                        color: #7D4B4B;
                    }
                }
                &.PHYSICS {
                    background-color: #A3B5D5;
                    & > p {
                        color: #556687;
                    }
                }
            }
        }
    }
`;