import styled from "styled-components";
import {urlBuilder} from "../../../additional.js";
import TeacherCard from "../TeacherCard/TeacherCard.jsx";

export default function ListTeachers(props){
    const list = props.list;
    if(list?.length === 0) return null;

    return (
        <ListTeachersStyled>
            {list.map((item, index) => (
                <TeacherCard key={index} teacher={item} />
            ))}
        </ListTeachersStyled>
    )
}

const ListTeachersStyled = styled.div`
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    position: relative;
    gap: 24px;
    box-sizing: border-box;
`;